import { GlobalComponent } from "../../components/global/global";
import { ModalController, LoadingController } from "ionic-angular";
import { Component } from "@angular/core";
import { String } from "../../Model/String";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { FileHttpHtml2CsvViewModel } from "../../Model/ViewModel/FileHttpHtml2CsvViewModel";
import { ConnServicesProvider } from "../../providers/conn-services/conn-services";

/**
 * Generated class for the FileHttpHtml2CsvModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-file-http-html2-csv-add-edit-modal",
  templateUrl: "file-http-html2-csv-add-edit-modal.html"
})
export class FileHttpHtml2CsvAddEditModalPage {

  item: FileHttpHtml2CsvViewModel;

  exec_file_seq:number=0;
  mode: string;
  CanEditBatch: boolean;
  title: string;
  FileHttpHtml2Csv_type: string;

  change_mode: string;
  help:string=``;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
    private global: GlobalComponent,
    public modalCtrl: ModalController,
    private ConnServices: ConnServicesProvider,
  ) {
    this.mode = navParams.data.mode;
    this.change_mode = navParams.data.change_mode;
    this.item = new FileHttpHtml2CsvViewModel();
    this.exec_file_seq = navParams.data.item.exec_file_seq;
    this.item.exec_file_seq = navParams.data.item.exec_file_seq;
    this.item.export_seq = navParams.data.item.export_seq;
    this.item.is_active = navParams.data.item.is_active;
    this.item.exec_seq = navParams.data.item.exec_seq;
    this.item.parameter_column_group =
      navParams.data.item.parameter_column_group;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;
    this.item.function_name = navParams.data.item.function_name;
    this.item.xpath = navParams.data.item.xpath;
    this.item.remote_conn_id = navParams.data.item.remote_conn_id;
    this.item.conn_string = navParams.data.item.conn_string;
    this.item.parameter_column_group =
      navParams.data.item.parameter_column_group;
    this.item.filename = navParams.data.item.filename;
    this.item.down_type_key = navParams.data.item.down_type_key;
    this.item.down_type = navParams.data.item.down_type;
    this.item.url= navParams.data.item.url;
    this.item.cookie=navParams.data.item.cookie;
    this.item.file_format_key = navParams.data.item.file_format_key;
    this.item.file_format = navParams.data.item.file_format;
    this.item.field_terminator = navParams.data.item.field_terminator;
    this.item.row_terminator = navParams.data.item.row_terminator;
    this.CanEditBatch = navParams.data.CanEditBatch;

    this.title =String.Format("{0}", this.exec_file_seq);
  }
  get_conn_string(remote_conn_id: string) {
  this.global.createLoader("取得連線字串中...");
    this.global.loading.present().then(() => {
      this.ConnServices.GetConnAsync(remote_conn_id).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.conn_string =
              data.Model.conn_string;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  SelectDownType() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.down_type_key,
      code_key: "061|4,061|5"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.down_type_key = select_data.code_key;
      this.item.down_type = select_data.code_desc;
      this.change_url();
    });

    modal.present();
  }

  SelectConn() {
  let modal = this.modalCtrl.create("ConnSelectModalPage", {
      select_remote_conn_id: this.item.remote_conn_id,
      conn_type_key: "065|004"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.remote_conn_id = select_data.remote_conn_id;
      this.item.conn_string = select_data.conn_string;
      this.change_url();
    });

    modal.present();
  }
  change_url()
{
  this.item.url=this.item.conn_string+this.item.function_name;
}
SelectFileFormat() {
  let modal = this.modalCtrl.create("CodeSelectModalPage", {
    select_key: this.item.file_format_key,
    code_key: "011|C,011|CB,011|UTF16,011|UTF32,011|UTF7,011|UTF8,011|UTF8B,011|W,011|B,011|W,011|WB"
  });
  modal.onDidDismiss(select_data => {
    if (select_data == null) return;
    this.item.file_format_key = select_data.code_key;
    this.item.file_format = select_data.code_desc;
  });

  modal.present();
}


  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }

  openNavParameterColumnPage(item) {
    this.global.createLoader("連線中...");
    this.global.loading.present().then(() => {
      this.navCtrl.push("ParameterColumnPage", { item: item });
    });
    this.global.dismissLoading();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad FileHttpHtml2CsvModalPage");
  }
}



