import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileHttpHtml2CsvListResponse } from "../../Model/Response/FileHttpHtml2CsvListResponse";
import { FileHttpHtml2CsvResponse } from "../../Model/Response/FileHttpHtml2CsvResponse";
import { FileHttpHtml2CsvViewModel } from '../../Model/ViewModel/FileHttpHtml2CsvViewModel';
import { StringResponse } from "../../Model/Response/StringResponse";

/*
  Generated class for the FileHttpHtml2CsvServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileHttpHtml2CsvServicesProvider {
  private FileHttpHtml2Csvs: FileHttpHtml2CsvListResponse;
  private FileHttpHtml2Csv: FileHttpHtml2CsvResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "FileHttpHtml2Csv";
    console.log("Hello FileHttpHtml2CsvServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
     sub_url = "FileHttpHtml2Csv?PageSize={1}&PageNumber={2}&keyword={3}&exec_file_seq={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_file_seq,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileHttpHtml2Csvs = new FileHttpHtml2CsvListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileHttpHtml2Csvs;
    });
  }



  public PostPutAsync(FileHttpHtml2CsvViewModel:FileHttpHtml2CsvViewModel,mode:string) {
    var sub_url: string;

    if(mode==="POST")
      sub_url = "FileHttpHtml2Csv";
    else
    sub_url = "FileHttpHtml2Csv/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileHttpHtml2CsvViewModel.export_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileHttpHtml2CsvViewModel,mode).map(
      item => {
        this.FileHttpHtml2Csv = new FileHttpHtml2CsvResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileHttpHtml2Csv;
      }
    );
  }

  public DeleteAsync(export_seq:number) {
    var sub_url: string;
    sub_url = "FileHttpHtml2Csv/{1}";
    sub_url = String.Format(sub_url, this.ctl,export_seq);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: ""
        });
      }
    );
  }

  public GetSqlAsync(export_seq:number,method:string) {
    var sub_url: string;
    sub_url = "FileHttpHtml2Csv/get_{2}_sql/{1}";
    sub_url = String.Format(sub_url, this.ctl,export_seq,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



